.photo-block {
  position: relative;
  display: grid;
  grid-template-rows: 158px max-content;
  width: 100%;
  font-family: "ptsans", sans-serif;
  font-style: normal;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  background-repeat: no-repeat;
  background-size: 100%;
  background-color: #e6e6e6;
  background-position-y: -45px;
  z-index: -1;
  margin: 0 auto;

  @media (min-width:$tablet-width) {
    background-image: url("#");
    grid-template-rows: 285px max-content;
    background-position-y: -51px;
    font-size: 30px;
    z-index: 1;
  }

  @media (min-width: $desktop-width) {

    background-image: url("#");
    grid-template-rows: 301px max-content;
    background-position-y: -56px;
  }

}

.photo-block__wrapper {
  background-image: url("#");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position-y: bottom;
  display: grid;
  grid-template-rows: 158px max-content;
  margin: 0;

  @media (min-width:$tablet-width) {

    background-image: url(".#");
    grid-template-rows: 290px max-content;
  }

  @media (min-width: $desktop-width) {

    background-image: url("#");
    grid-template-rows: 290px max-content;
  }

}

.photo-block__image {
  margin: 0 auto;
  margin-top: 52px;
  display: block;

  @media (min-width:$tablet-width) {
    margin-top: 101px;
    width: 459px;
    height: 79px;
  }

}

.photo-block__text {
  text-align: center;
  margin: 0;
  padding: 24px 10px 50px 10px;
  margin-top: auto;
  background-color: #ffffff;

  @media (min-width:$tablet-width) {
    padding: 40px 155px 67px 155px;
  }

}

.photo-block__title {
  font-size: 18px;
  margin-bottom: 23px;

  @media (min-width:$tablet-width) {
    font-size: 30px;
    padding-top: 5px;
    padding-bottom: 9px;
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    padding-top: 5px;
    padding-bottom: 9px;
  }
}

.photo-block__feature {
  font-family: 'ptsans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;

  @media (min-width: $desktop-width) {
    margin-bottom: 20px;
  }


}
